import { __decorate } from "tslib";
import Vue from 'vue';
import { Mixin, Mixins } from 'vue-mixin-decorator';
import { bannersMapper } from '../module/store';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapGetters(['loadingError'])
    }
});
let MsgBoxTools = class MsgBoxTools extends Mixins(Mappers, MsgBoxToolsApp) {
    constructor() {
        super(...arguments);
        this.requestStatus = 'failFetchList';
        this.titles = { 'success': '!', 'fail': '!', 'other': null };
        this.statuses = {
            success: ['successCreate', 'successEdit', 'successDelete'],
            fail: ['failFetchList', 'failFetchBanner', 'failCreate', 'failEdit', 'failDelete', 'failDeactivate', 'failSetAmount'],
            other: ['beforeDelete']
        };
        this.btns = {
            successCreate: { firstBtn: ' ', secondBtn: '  ' },
            successEdit: { firstBtn: ' ', secondBtn: '  ' },
            successDelete: { firstBtn: '  ' },
            failFetchList: { firstBtn: ' ', secondBtn: '  ' },
            failFetchBanner: { firstBtn: '  ' },
            failCreate: { firstBtn: ' ', secondBtn: '' },
            failEdit: { firstBtn: ' ', secondBtn: '' },
            failDelete: { firstBtn: ' ', secondBtn: '' },
            failDeactivate: { firstBtn: ' ', secondBtn: '' },
            failSetAmount: { firstBtn: ' ', secondBtn: '' },
            beforeDelete: { firstBtn: '', secondBtn: '' }
        };
        this.msgBoxOtherTitles = { beforeDelete: '  ?' };
        this.msgBoxSuccessMsgs = { successCreate: '  ', successDelete: '   ', successEdit: '  ' };
        this.msgBoxOtherMsgs = { beforeDelete: '        ' };
    }
    get statusType() {
        for (const type in this.statuses) {
            if (this.statuses[type].indexOf(this.requestStatus) >= 0)
                return type;
        }
    }
    get msgBoxTitle() { return this.statusType === 'other' ? this.msgBoxOtherTitles[this.requestStatus] : this.titles[this.statusType]; }
    get msgBoxMsg() {
        if (this.statusType === 'other')
            return this.msgBoxOtherMsgs[this.requestStatus];
        else if (this.statusType === 'success')
            return this.msgBoxSuccessMsgs[this.requestStatus];
        else
            return this.loadingError;
    }
    get msgBoxBtns() { return this.btns[this.requestStatus]; }
    get msgBoxContent() { return { title: this.msgBoxTitle, msg: this.msgBoxMsg || this.contentDefault, ...this.msgBoxBtns }; }
};
MsgBoxTools = __decorate([
    Mixin
], MsgBoxTools);
export default MsgBoxTools;
